@import url("https://fonts.googleapis.com/css2?family=Lato&family=Neucha&family=Raleway&family=Roboto&display=swap");

.App {
    text-align: center;
    display: flex;
    max-height: 100vh;
    text-transform: capitalize;
    font-family: "Lato", "sans-serif";
}

.right-container {
    height: 100vh;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.pokemon-types {
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
}

.type-icon {
    border-radius: 10px;
    padding: 3px 6px;
    margin: 1%;
    font-size: 2.5vmin;
    min-width: 75px;
    color: #fff;
}

.navigation-bar {
    background-color: #5b6368;
    display: flex;
    height: 5vh;
    padding-left: 10%;
    padding-right: 10%;
    justify-content: space-between;
    align-items: center;
}

.navigation-bar button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 2rem;
    color: #fff;
}

@media only screen and (max-width: 500px) {
    .App {
        display: flex;
        flex-direction: column;
    }
}
